
import React from 'react'
import AwesomeSlider from 'react-awesome-slider';
import '../../../node_modules/react-awesome-slider/dist/custom-animations/cube-animation.css';

export default function Carousel() {

    return (
        <AwesomeSlider
            play={true}
            cancelOnInteraction={false}
            interval={1000}
            infinite={true}
        >
            <div data-src="/Image/ImageSlides/1.jpg" />
            <div data-src="/Image/ImageSlides/2.jpg" />
            <div data-src="/Image/ImageSlides/3.jpg" />
        </AwesomeSlider>
    )
}
